import React,{useState,useEffect} from 'react'
import SubMenuItem from './SubMenuItem';
import CartProductList from './CartProductList'
import api from '../../data';
import BigButton from '../../commonComponents/BigButton'
import Cookies from 'js-cookie'
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
export default function SubMenu({
    languages,
    subList,
    totalText,
    buttons,
    contentEmpty,
    user,
    setUser,
    isCart,
    isChange,
    setIsChange
    }) {

      
    const [cartTotalPrice,setcartTotalPrice] = useState(0);
    const axios = require('axios').default;
    const {apiCart,apiTotalCartPrice} = api
    const [cart,setCart] = useState([])
    const data = new FormData()
    data.append("user_id",Cookies.get("user_id"))

    const fetchData = async () => {     
        try {
            const carts = await axios.post(apiCart,data);
            setCart(carts.data);
        } catch(error) {
            setCart([])
        }
    }



    useEffect(() => {
        fetchData();
        
    },[Cookies.get("user_id"),isChange])
    


    const logOut = () => {
        setTimeout(() => {
            setUser("");
            Cookies.remove('user')
            Cookies.remove('user_id')
            setCart([])
            setIsChange("")
        },200)
        toast.error('Đăng xuất thành công!', {
            position: "top-center",
            autoClose: 3000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        }) 
    }
   

    
    return (
        <div className="sub_menu">
            <div className="arrow-wrapper wrap">
                <div className="arrow-up"></div>

            </div>
            <div className="arrow-wrapper">
                <div className="arrow-up "></div>
            </div>
            <ul className="sub_menu-list">
                {languages && 
                    languages.map((item,index) => {
                        const {id,language,icon,link} = item;
                            return (
                                language && 
                                    <SubMenuItem {...item} key={index}/> 
                            )
                        })           
                }
                {
                    user && 
                    <>
                        <div className="sub_user-wrapper">
                            <p className="sub_user">{user}</p>
                        </div>
                        <div className="sub_user-logout">
                            <BigButton type="warning" text="ĐĂNG XUẤT" onClick={logOut}/>
                        </div>
                    </>
                }
                {isCart && cart.length > 0 ?
                    <CartProductList 
                    cart={cart} 
                    totalText={totalText} 
                    buttons={buttons} 
                    cartTotalPrice={cartTotalPrice}
                    isChange={isChange}
                    setIsChange={setIsChange}
                    />
                    :
                    <div className="none-product-wrapper">
                        <span className="none-product">{contentEmpty}</span>
                    </div>
                }
                {subList && 
                        subList.map((item,index) => {
                            return (
                                <SubMenuItem {...item} key={index}/>
                            )
                        })
                }
            </ul>
        </div> 
    )
}
