import React,{ useState }from 'react'
import CartProductItems from './CartProductItems'
import BigButton from '../../commonComponents/BigButton'
import  formatNumber  from '../../functions/formarNumber'
import Cookies from 'js-cookie'
export default function CartProductList({
    cart,
    totalText,
    buttons,
    isChange,
    setIsChange
    }) {
    
       const getTotalQuantity = (items) => {
            return formatNumber(items.reduce((total, item) => Number(item.product_price) + total, 0))
          }
        
    
    const style = {
        overflowY: 'scroll',
    }
    
    const amountProduct = {};
    cart.forEach(function (x) { amountProduct[x.product_id] = (amountProduct[x.product_id] || 0) + 1; });
    const newCart = [...new Map(cart.map(item =>
        [item["product_id"], item])).values()];


    


 
    return (
    <div className="cart-wrapper">
        <div className="cart">
            <ul className="product-list" style={cart.length > 2 ? style : undefined}> 
                {newCart.map((product,index) => {

                    
                    return <CartProductItems 
                    amountProduct={amountProduct[product.product_id]}
                    {...product} 
                    key={index}
                    formatNumber={formatNumber}
                    isChange={isChange}
                    setIsChange={setIsChange}
                    />
                })}
                
            </ul>
            <div className="cart-total-price flex">
                <span>{totalText}&nbsp;</span>
                <span className="cart-price-total">{getTotalQuantity(cart)} 
                    <span className="cart-price-unit unit"></span>
                </span>
            </div>
            <div className="cart-button">
                {buttons && buttons.map((button,index) => {
                    return (
                        <BigButton key={index} {...button}/>
                    )
                })}
            </div>
        </div>
    </div>
    )
}
