import React,{useState,useEffect} from 'react'
import './header.css'
import Logo from './components/Logo'
import HomeMenu from './components/HomeMenu'
import MiniMenu from './components/MiniMenu'
import  Login  from '../login/Login'
import { homeMenu,miniMenu } from '../data'
import Cookies from 'js-cookie'
export default function Header({
    isChange,
    cartTotalPrice,
    isNotValid,
    setIsNotValid,
    isEnable,
    handleIsEnable,
    handleDisable,
    setIsChange,
    isSubmitted,
    setIsSubmitted,
    }) {

    const [offset, setOffset] = useState(0);
   
    const [user,setUser] = useState(Cookies.get('user') ?? null);
   

   
     
    useEffect(() => {
        window.onscroll = (e) => {
          setOffset(window.pageYOffset)
        }
      }, [])
    
     
    
    return (
        <header className={offset > 125 ? 'header dropdown-header' :  offset === 0 ? 'header' : 'header dropdown-header'} >
            <div className="header-wrapper" >
                <div className="header-content container"> 
                    <Logo />
                    <HomeMenu homeMenu={homeMenu}/>
                    <MiniMenu 
                    miniMenu={miniMenu} 
                    cartTotalPrice={cartTotalPrice} 
                    isEnable={isEnable} 
                    user={user}
                    setUser={setUser}
                    handleIsEnable={handleIsEnable}
                    isChange={isChange}
                    setIsChange={setIsChange}
                    />
                </div>
            </div>
            <Login 
            setIsSubmitted={setIsSubmitted}
            isEnable={isEnable} 
            setUser={setUser}
            handleDisable={handleDisable}
            isNotValid={isNotValid}
            setIsNotValid={setIsNotValid}
            />
        </header>
    )
}
