import React from 'react'

export default function BigButton({
    hoverDisabled,shape = "square",
    color = "var(--white-color)",
    background,
    width = "auto",
    type = "default",
    text,
    fontSize,
    padding,
    margin,
    position,
    onClick,
    borderRadius,
    border,
    marginLeft,
    height,
    }) {

    const btnType = `btn big-btn btn-${type}`
    const btnText = text
    return (
        <button className={btnType} style={{
            marginLeft: marginLeft,
            borderRadius: shape === "soft" && "18px",
            color: color,
            width: width,
            pointerEvents: hoverDisabled && "none",
            fontSize: fontSize,
            padding: padding,
            margin: margin,
            position: position,
            background: background,
            border: border === true && "1px solid rgba(0,0,0,0.08)" ,
            height: height
            }}
        onClick={onClick}>
            <span>
                {btnText}
            </span>
        </button>
    )
}
